<template>
  <div class="page">
    <header>
      <Topbar/>
    </header>
    <main>
      <ResumeEditor/>
      <ResumePreview/>
    </main>
  </div>
</template>

<script>
import Topbar from './components/Topbar'
import ResumeEditor from './components/ResumeEditor'
import ResumePreview from './components/ResumePreview'

import 'normalize.css'
import './assets/reset.css'



export default {
  name: 'app',
  data: function() {
    return {
      text: '你好'
    }
  },
  components: { Topbar, ResumeEditor, ResumePreview }
}
</script>

<style lang="scss">
.page {
  height: 100vh;
  display: flex;
  flex-direction: column;
  background: #EAEBEC;
  >main {
    min-width: 1024px;
    max-width: 1440px;
    margin: 0;
    display: flex;
    flex-grow: 1;
    justify-content: space-between;
    padding: 0 16px;
    margin-top: 16px;
    margin-bottom: 16px;
    width: 100%;
    align-self: center;
    >#resumeEditor {
      width: 35%;
      background: #444;
    }
    >#resumePreview {
      flex-grow: 1;
      margin-left: 16px;
      background: #777;
    }
  }
}
</style>



