<template>
    <div id="resumeEditor">
        <nav>
            <ol>
                <li v-for="(item,index) in resume.visibleItems" :class="{active: item === selected}" @click="selected = item">
                    {{index}}
                </li>
            </ol>
        </nav>
        <ol class="panels">
            <li v-for="item in resume.visibleItems" v-show="item===selected">
                {{resume[item]}}
            </li>

        </ol>
    </div>
</template>
<script>
export default {
    name: 'ResumeEditor',
    data: function() {
        return {
            selected: 'bio',
            resume: {
                visibleItems: ['bio', 'work history', 'education', 'projects', 'awards', 'contacts', 'others'],
                bio: {
                    name: '',
                    city: '',
                    title: ''
                },
                'work history': [],
                education: [],
                projects: [],
                awards: [],
                contacts: [],
                others: []
            }
        }
    },
    methods: {

    }
}
</script>
<style lang="scss">
#resumeEditor {
    background: #ffffff;
    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.25);
    display: flex;
    flex-direction: row;
    >nav {
        width: 80px;
        background: black;
        color: white;
        >ol {
            list-style: none;
            >li {
                height: 48px;
                display: flex;
                justify-content: center;
                align-items: center;
                margin-top: 16px;
                margin-bottom: 16px;
                text-align: center;
                cursor: pointer;
                &.active {
                    background: white;
                    color: #000;
                }
            }
        }
    }
}
</style>


